<svelte:head>
	<script src="https://kit.fontawesome.com/7e1024d596.js"></script>
	<style>
		.visua11y-annotate{
			position: absolute;
			font-size: 12px;
			padding: 2px;
			background: green;
			z-index: 8888;
		}

		.visua11y-annotate-bg{
			border: 3px dotted green !important;
		}
	</style>
</svelte:head>
<script>
	import { onMount } from 'svelte';
	import ConfirmanceDetails from './ConfirmanceDetails.svelte';
 
	export let name;
	
	let confirmanceData = [
		{
			level: "wcag2a",
			heading: "WCAG 2.0",
			subheading: "Level A",
			logo: "https://www.w3.org/WAI/wcag2A",
			link: "https://www.w3.org/WAI/WCAG2A-Conformance",
			alt: "Level A conformance, W3C WAI Web Content Accessibility Guidelines 2.0",
			title: "Explanation of WCAG 2.0 Level A Conformance",
			scanTags: ['wcag2a'],
			scanData: {}
		},
		{
			level: "wcag2aa",
			heading: "WCAG 2.0",
			subheading: "Level AA",
			logo: "https://www.w3.org/WAI/wcag2AA",
			link: "https://www.w3.org/WAI/WCAG2AA-Conformance",
			alt: "Level Double-A conformance, W3C WAI Web Content Accessibility Guidelines 2.0",
			title: "Explanation of WCAG 2.0 Level Double-A Conformance",
			scanTags: ["wcag2a", "wcag2aa"],
			scanData: {}
		},
		{
			level: "wcag2aaa",
			heading: "WCAG 2.0",
			subheading: "Level AAA",
			logo: "https://www.w3.org/WAI/wcag2AAA",
			link: "https://www.w3.org/WAI/WCAG2AAA-Conformance",
			alt: "Level Triple-A conformance, W3C WAI Web Content Accessibility Guidelines 2.0",
			title: "Explanation of WCAG 2.0 Level Triple-A Conformance",
			scanTags: ["wcag2a", "wcag2aa", "wcag2aaa"],
			scanData: {}
		},
		{
			level: "wcag21a",
			heading: "WCAG 2.1",
			subheading: "Level A",
			logo: "https://www.w3.org/WAI/WCAG21/wcag2.1AA-v",
			link: "https://www.w3.org/WAI/WCAG2.1AA-Conformance",
			alt: "Level Double-A conformance, W3C WAI Web Content Accessibility Guidelines 2.1",
			title: "Explanation of WCAG 2.1 Level Double-A Conformance",
			scanTags: ['wcag2a', 'wcag2aa', 'wcag21a', 'wcag21aa'],
			scanData: {}
		},
		{
			level: "section508",
			heading: "Section 508",
			subheading: "Section 508",
			logo: "http://digitalinclusionnewslog.itu.int/wp-content/uploads/sites/9/2015/10/Section-508.png",
			link: "https://www.access-board.gov/guidelines-and-standards/communications-and-it/about-the-ict-refresh/final-rule/text-of-the-standards-and-guidelines",
			alt: "Level Double-A conformance, W3C WAI Web Content Accessibility Guidelines 2.1",
			title: "Explanation of WCAG 2.1 Level Double-A Conformance",
			scanTags: ['section508'],
			scanData: {}
		}
	]

	let scanQueue = [];
	let scanInProgress = false;
	let fullContainer = false;
	let levelContainer = false;
	let selectedLevelItem;
	function toggleContainer(){
		fullContainer = !fullContainer;
	}

	function runScan(item){
		if(scanInProgress){
			scanQueue.push(item)
		} else {
			scanInProgress = true;
			item.scanData = {};
			axe.run(document, {runOnly: item.scanTags}, (err, results) => {
				item.scanData = results;
				scanInProgress = false;
				if(scanQueue.length){
					const d = scanQueue.pop();
					runScan(d);
				}
			});
		}
	}

	function toggleLevelDetails(item){
		if(levelContainer){
			levelContainer = false;
			selectedLevelItem = undefined;
		} else {
			selectedLevelItem = item;
			levelContainer = true;
		}
	}

	onMount(() => {
		const scr2 = document.createElement('script');
		scr2.src = "http://localhost:5000/axe.min.js";
		scr2.onload = () => {
			console.log('AXE Tools loaded');
			confirmanceData.forEach(item => {
				runScan(item);
			});
		}
 		document.querySelector('head').appendChild(scr2);
	});
</script>

<style>
	.visua11y {
		width: auto !important;
		height: auto !important;
		max-height: 450px;
		max-width: 700px;
	}

	.visua11y{
		font-family: Arial, Helvetica, sans-serif !important;
		color: #ffffff !important
	}
	
	.visua11y-toolbar {
		background-color: #09304b !important;
		color: #f2f2f2 !important;
		position: fixed !important;
		top: auto !important;
		right: auto !important;
		bottom: 0 !important;
		left: 10px !important;
		border-top-left-radius: 5px !important;
		border-top-right-radius: 5px !important;
		overflow: scroll !important;
		z-index: 9998 !important;
		font-family: Arial, Helvetica, sans-serif !important;
	}
	
	.visua11y-main-ctr{}

	.visua11y-minimised-ctr{
		font-size: 28px;
	}

	.visua11y-button{
		background: none;
		border-style: none;
		color: white !important;
		font-size: inherit !important

	}

	.visua11y-main-ctr-minimise{
		float: right;
		font-size: 14px; 
	}
	.visua11y-level-ctr{
		float: left;
		padding: 20px;
		max-width: 20%;
		font-size: 14px !important;
	}
	.visua11y-ctr-link a{
		font-weight: bold;
		color: #ffffff !important;
		text-decoration: underline !important;
	}
	
</style>

<div class="visua11y visua11y-toolbar">
	{#if !fullContainer}
		<div class="visua11y-minimised-ctr">
			<button class="visua11y-button" on:click={toggleContainer}>
				<span><i class="fas fa-binoculars"></i></span>
			</button>
		</div>
	{/if}
	{#if fullContainer}
			<div class="visua11y-main-ctr">
				<div class="visua11y-main-ctr-minimise">
					{#if levelContainer}
						<strong> 
							<button class="visua11y-button" on:click={toggleLevelDetails}>
								<span>back</span>
							</button>
						</strong>
					{/if}
					<strong> 
						<button class="visua11y-button" on:click={toggleContainer}>
							<span><i class="fas fa-binoculars"></i></span>
						</button>
					</strong>
				</div>
				{#if levelContainer}
					<ConfirmanceDetails bind:levelData={selectedLevelItem}></ConfirmanceDetails>
				{/if}
				{#if !levelContainer}
					{#each confirmanceData as confirmanceItem}
						<div class="visua11y-level-ctr">
							<div>
								<strong>{confirmanceItem.heading}</strong>
							</div>
							<div>
								<strong>{confirmanceItem.subheading}</strong>
							</div>
							<div>
								<a href={confirmanceItem.link}
									target="_blank"
									title={confirmanceItem.title}>
									<img height="32" width="88"
										src={confirmanceItem.logo}
										alt={confirmanceItem.alt}>
								</a>
							</div>
							{#if confirmanceItem.scanData && confirmanceItem.scanData.violations}
								<div>
									<span>
										<i class="fas fa-times-circle"></i>
										{confirmanceItem.scanData.violations.length} violations
									</span>
								</div>
								<div class="visua11y-ctr-link">
									<a href="javascript:void(0)" on:click={() => toggleLevelDetails(confirmanceItem)}> Details </a>
								</div>
								<div class="visua11y-ctr-link">
									<a href="javascript:void(0)" on:click={() => runScan(confirmanceItem)}> Run again </a>
								</div>
							{/if}
						</div>
					{/each}
				{/if}
			</div>
		{/if}
</div>

