<script>
    import { onMount } from 'svelte';
    export let levelData;

    let totalNodes = 0;
    let totalNodesComputed = false;
    let selectedViolationIdx = -1;
    let isViolationDetailsSelected = false;

    let confirmanceRulesCount;
    let pageApplicableRulesCount;
    let pageInapplicableRulesCount;

    $: {
        levelData.scanData.violations.forEach(v => {
            v.show = false;
            if(!totalNodesComputed) {
                totalNodes += v.nodes.length;
                v.nodes.forEach(node => {
                    const nodeElem = document.querySelector(node.target);
                    const annotationElem = document.createElement('div');
                    annotationElem.innerHTML = nodeElem.nodeName;
                    annotationElem.className = "visua11y-annotate";
                    if(nodeElem.nodeName !== 'HTML'){
                        nodeElem.insertAdjacentElement('beforeBegin', annotationElem);
                    }
                    node.showHtml = false;
                })
            }
        });

        levelData.violations = levelData.scanData.violations
        totalNodesComputed = true;
        confirmanceRulesCount = axe.getRules(levelData.scanTags).length;
        pageInapplicableRulesCount = levelData.scanData.inapplicable.length;
        pageApplicableRulesCount = confirmanceRulesCount - pageInapplicableRulesCount;

    }

    function toggleViolationDetails(violation, idx){
        if(!isViolationDetailsSelected || (idx != -1 && idx !== selectedViolationIdx)){
            isViolationDetailsSelected = true;
            selectedViolationIdx = idx;
        } else if(idx == selectedViolationIdx){
            isViolationDetailsSelected = false;
            selectedViolationIdx = -1;
        }
    }

    function gotoElement(target){
        const annotatedElems = document.querySelectorAll('.visua11y-annotate-bg');
        for(let i=0; i<annotatedElems.length; i++){
            annotatedElems[i].className = annotatedElems[i].className.replace(' visua11y-annotate-bg', '');
        }
        if(target){
            const ele = document.querySelector(target);
            if(ele){
                ele.className = ele.className +' visua11y-annotate-bg';
                ele.scrollIntoView();
            }
        }
    }

    function showHtml(node){
        node.showHtml = !node.showHtml;
        return node.showHtml;
    }
</script>

<style>
    .level-container{
        padding: 15px;
        font-size: 14px !important;
        clear: both;
    }
    .level-container code {
        padding: 2px 4px;
        font-size: 90%;
        color: #c7254e;
        background-color: #f9f2f4;
        border-radius: 4px;
    }

    .media-block{
        padding: 15px;
        float: left;
    }

    .level-description{
        padding: 15px;
        clear: both;
    }

    .level-violations{
        padding: 10px;
        
    }
    .media-block-left{
        float: left;
    }
    .media-block-right{
        float: right;
        padding-left: 30px;
    }

    .media-block-right ul {
        list-style: initial;
    }

    .level-violations{
        clear:both;
    }
    .level-violations a {
        color: white !important;
        text-decoration: underline !important;
    }

    .violation-node {
        padding: 10px;
        clear: both;
    }

</style>
<div class="level-container">
    <div class="media-block">
        <div class="media-block-left">
            <div>
                {levelData.heading}
            </div>
            <div>
                {levelData.subheading}
            </div>
            <div>
                <a href={levelData.link}
                    target="_blank"
                    title={levelData.title}>
                    <img height="32" width="88"
                        src={levelData.logo}
                        alt={levelData.alt}>
                </a>
            </div>
        </div>
        <div class="media-block-right">
            <ul>
                <!-- <li>There are 78 guidelines defined for this confirmance level</li> -->
                <li>This tool runs {confirmanceRulesCount} rules for this level</li>
                <li>Only {pageApplicableRulesCount} out of the {confirmanceRulesCount} rules apply to this page</li>
            </ul>
        </div>
    </div>
    <div class="level-description">
        <div>
            <span>
                <i class="fas fa-exclamation-circle"></i>
                <strong>{totalNodes}</strong> html elements with <strong>{levelData.violations.length}</strong> violations
            </span>
        </div>
        {#each levelData.violations as violation, i}
            <div class="level-violations">
                <div>
                    <i class="fas fa-angle-right"></i>
                    <strong>{violation.help}</strong> 
                    <a href="javascript:void(0)" on:click={() => toggleViolationDetails(violation, i)}> Details</a>
                </div> 
                {#if isViolationDetailsSelected && selectedViolationIdx === i}
                    {#each violation.nodes as node}
                        <div class="violation-node">
                            <div>
                                Impact: {node.impact}
                            </div>
                            <div>
                                Target: 
                                <a href="javascript:void(0)" on:click={() => gotoElement(node.target)}>
                                    <code>{node.target}</code>
                                </a>
                                <a href="javascript:void(0)" on:click={() => showHtml(node)}> Show html</a>
                                <a href="javascript:void(0)" on:click={() => gotoElement(node.target)}> Go to element</a>
                                {node.showHtml}
                            </div>
                            {#if false}
                                <div>
                                   <code>{node.html}</code>
                                </div>
                            {/if}
                            <div> Fix this:<br/>
                                <ul>
                                    {#each node.any as checkedNode}
                                        <li>
                                            <i style="font-size: 6px;padding-left: 10px;" class="fas fa-minus"></i>
                                            {checkedNode.message}
                                        </li>
                                    {/each}
                                </ul> 
                            </div>
                            <!-- <div> Impact:<br/> {node.impact} </div>
                            <div> To solve this:<br/>
                                <ul>
                                    {#each node.any as checkedNode}
                                        <li>{checkedNode.message}</li>
                                    {/each}
                                </ul> 
                            </div>
                            <div> Element Target:<br/> <code>{node.target}</code> </div>
                            <div> Violated Element html:<br/> <code>{node.html}</code> </div>     -->
                        </div>
                    {/each}
                {/if}
            </div>
        {/each}
    </div>
</div>


